.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 2rem;
}
.landing {
  @include flexbox(row, space-evenly);
  flex-wrap: wrap;
  padding: 2rem;
  margin-bottom: 8rem;

  &__info {
    position: relative;
    max-width: 40rem;
    width: 100%;

    .info-title {
      font-size: $hero-heading;
      margin-bottom: 2rem;
      color: $primary-dark;
      text-transform: capitalize;
      max-width: 38rem;
    }

    .info-subtitle {
      margin-bottom: 2rem;
      @extend %paragraph;
      max-width: 32rem;
    }
  }
  &__img {
    max-width: 40rem;
    width: 100%;
    img {
      width: 100%;
    }
  }

}

.landing > img {
    position: absolute;
    max-width: 55rem;
    width: 100%;
    z-index: -100;
    left: -10rem;
    top: -10rem;
  }

