.about {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 8rem;
  &__header {
    @extend %primary-heading;
  }
  &__body {
    margin: 0 auto;
    max-width: 80rem;
    @extend %paragraph;
  }
}

.projects {
    margin: 4rem 0;
}
.cards-container {
  @include flexbox(row, center);
  flex-wrap: wrap;

}
