.project {
  @include flexbox(column, center);

  &__title {
    @extend %primary-heading;
  }
  &__logo {
    @extend %secondary-heading;
    text-transform: capitalize;
    display: flex;
    align-items: center;
  }
  .avatar {
    display: inline-block;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: $secondary-red;
    text-align: center;
    line-height: 4rem;
    margin-right: 1rem;
    text-align: center;
    border: 0.2rem $secondary-red solid;
    img {
      width: 100%;
    }
  }
  &__shots {
    @include flexbox(row, flex-start);
    margin-bottom: 2rem;
    overflow-x: auto;
    scroll-snap-type: x mandatory;

    & > div {
      position: relative;
      min-width: 20rem;
      height: 30rem;
      background-color: $primary-light;
      scroll-snap-align: center;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50% , -50%);
      }
    }
    & > * + * {
      margin-left: 2rem;
    }
  }

  &__discription {
    margin-bottom: 2rem;
    @extend %paragraph;
    .discription-title {
      text-transform: capitalize;
      color: $primary-dark;
      margin-bottom: 2rem;
    }
    .discription-body {
      max-width: 80rem;
    }
  }
  &__team {
    @include flexbox(row, flex-start);
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    padding: 4rem;
    .team__card {
      @include flexbox(column, space-between);
      background-color: #fff;
      box-shadow: 0.1rem 0 1.7rem rgba(0, 0, 0, 0.159);
      padding: 1rem;
    }
    & > div {
      min-width: 20rem;
      width: 20rem;
      height: 26rem;
      border-radius: 0.6rem;
      background-color: $primary-light;
      scroll-snap-align: center;
    }
    & > * + * {
      margin-left: 2rem;
    }
    margin-top: 4rem;

    .card__title {
      @include flexbox(row, flex-start);

      text-align: start;
      width: 100%;
      margin-bottom: 1rem;
      .member {
        @extend %paragraph;
        font-weight: bold;
        text-transform: capitalize;
        color: $primary-dark;
      }
    }
    .card__body {
      @extend %paragraph;
    }
  }
}
@media screen and (max-width: 992px) {
  .project {
    margin-top: 4rem;
    display: block;
  }
}
