.contact {
  @include flexbox(row, space-around);
  flex-wrap: wrap-reverse;
  &__img {
    max-width: 40rem;
    width: 100%;
    margin-bottom: 2rem;
    img {
      width: 100%;
    }
  }
  &__form {
    max-width: 40rem;
    width: 100%;
    &__title {
      @extend %primary-heading;
    }
    .form {
      & > div {
        position: relative;
      }
      & > div > img {
        width: 3rem;
      }
      &__btn {
          text-align: center;
          button {
            @include flexbox(row, space-betweeb);
            margin: 0 auto;
            img {
              width: 2.5rem;
              margin-left: 1rem;
            }
          }
      }
      &__name,
      &__email,
      &__message {
        margin-bottom: 2rem;
        label {
          display: block;
          @extend %secondary-heading;
          margin-bottom: 0;
        }
        input {
          width: 100%;
          border: none;
          padding: 2rem 1rem;
          border-bottom: 0.3rem #ccc solid;
          &:focus {
            outline: none;
            border-color: $secondary-blue;
          }
        }
        img {
          position: absolute;
          top: 1rem;
          right: 0;
          // transform: translateY(-50%);
        }
      }
      &__message {
          textarea {
            width: 100%;
            border: none;
            padding: 2rem 1rem;
            border-bottom: 0.3rem #ccc solid;
            &:focus {
              outline: none;
              border-color: $secondary-blue;
            }  
          }
      }
    }
  }
}
